function distanceBetween(start, end) {
    var sum = Math.pow(end[0] - start[0], 2) + Math.pow(end[1] - start[1], 2);
    return Math.pow(sum, 1 / 2);
}
function degreeBetween(start, end, mode) {
    var radian = Math.atan2(start[1] - end[1], start[0] - end[0]);
    if (mode !== 'DEGREES') {
        return radian;
    }
    var angle = 180 / Math.PI * radian;
    return angle;
}
function promisifyToBlob(canvas, type, quality) {
    return new Promise(function (resolve) {
        canvas.toBlob(function (blob) {
            resolve(blob);
        }, type, quality);
    });
}
export { distanceBetween, degreeBetween, promisifyToBlob };
